<template>
  <!-- 编辑表单对话框 -->
  <EditForm ref="editFormRef" @refresh="refreshData"/>
  
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="产品名称">
            <el-input 
              v-model="searchForm.product_name" 
              placeholder="按产品名称搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="状态">
            <el-select v-model="searchForm.status" placeholder="选择状态" clearable>
              <el-option label="全部" :value="''" />
              <el-option label="启用" :value="1" />
              <el-option label="禁用" :value="0" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 搜索按钮和重置按钮 -->
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
          <el-button type="primary" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新、添加、删除按钮 -->
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>
    <!-- 代理商不显示添加和删除按钮 -->
    <el-button v-if="!isAgent" type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
    <el-button v-if="!isAgent" type="danger" :disabled="!hasSelectedRows" @click="handleBatchDelete('/api/v1/admin/payment-type/delete')">删除</el-button>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;" 
        @selection-change="handleSelectionChange">
      <!-- 表格列定义 - 代理商不显示选择框 -->
      <el-table-column v-if="!isAgent" type="selection" width="40"/>
      <el-table-column prop="product_name" label="产品名称" align="center" min-width="150"/>
      <el-table-column prop="product_code" label="产品代码" align="center" min-width="180"/>
      <el-table-column prop="description" label="产品描述" align="center" min-width="250" show-overflow-tooltip/>
      <el-table-column prop="sort_order" label="排序" align="center" min-width="90"/>

      <!-- 状态列 - 代理商只显示状态，不能切换 -->
      <el-table-column label="状态" align="center" min-width="90">
        <template #default="{ row }">
          <el-switch 
            v-if="!isAgent"
            v-model="row.status" 
            :active-value="1" 
            :inactive-value="0" 
            @change="toggleSwitch('/api/v1/admin/payment-type/switch', row.id)"
          />
          <el-tag v-else :type="row.status === 1 ? 'success' : 'info'">
            {{ row.status === 1 ? '启用' : '禁用' }}
          </el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="150"/>

      <!-- 操作列 - 代理商不显示操作列 -->
      <el-table-column v-if="!isAgent" label="操作" align="center" fixed="right" min-width="200">
        <template #default="scope">
          <el-button size="small" @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button size="small" type="danger" @click="handleDelete('/api/v1/admin/payment-type/delete', scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { Plus, Refresh, Search } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent, computed } from 'vue';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入编辑
const EditForm = defineAsyncComponent(() => import('./edit.vue'));
// 导入编辑页面
const editFormRef = ref(null);
const openEditFrom = (row = {}) => {
  console.log(row);
  if (editFormRef.value) {
    editFormRef.value.open({ ...row }); // 传递 `row` 作为参数
  } else {
    console.error("EditForm 组件未正确挂载！");
  }
};

// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch,
  resetSearchParams
} = useTableDataPlugin("/api/v1/admin/payment-type/list");

// 搜索表单数据
const searchForm = ref({
  product_name: '',
  status: ''
});

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    product_name: '',
    status: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 获取用户信息判断是否是代理商
const userInfo = ref<any>({});
const isAgent = computed(() => {
  // 使用后端返回的 is_agent 字段，更安全
  return userInfo.value?.is_agent === true;
});

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 在组件挂载时加载数据
onMounted(() => {
  getUserInfo();
  loadData();
});

// 删除方法：单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api, [row.id]);  // 传递一个 id 数组来删除单条记录
};

// 批量删除
const handleBatchDelete = (api: string) => {
  deleteRows(api);
};

</script>

